.slanted-div {
  border-radius: 10px;
  margin: 1vh 14vh;
  position: relative;
  padding: 10px 0;
  background: #f3effe;
  overflow: visible;
  z-index: 0;
}

/* where the magic happens */
.slanted-div:before, .slanted-div:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background: inherit;
  z-index: -1;
  top: 0;
  /* transform-origin: right top;
  transform: skewY(-3deg); */
}

.slanted-div:after {
  bottom: 0;
  /* transform-origin: right bottom;
  transform: skewY(3deg); */
}

/* displays the content inside, as these settings in the parent breaks the effect */
.slanted-div div {
  text-align: center;
  /* font-size: 1.5em; */
  line-height: 1.5;
}

